@import url("https://fonts.googleapis.com/css?family=Do+Hyeon&display=swap&subset=korean");

.App {
  position: fixed;
  width: 100vw;
  height: 100vh;
  font-family: "Do Hyeon";
}

@media (max-width: 450px) {
  h1 {
    font-size: 7vmax;
  }
  h2 {
    font-size: 5vmax;
  }
  h6 {
    font-size: 2.3vmax;
  }

  .header {
    height: 12%;
    padding-left: 5%;
    display: flex;
    flex-direction: column;
    justify-content: center;
  }

  .body_background {
    height: 60%;
    padding: 5%;
  }
  .body {
    height: 100%;
  }
  .body_title {
    height: 30%;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
  }
  .body_title_inline {
    display: flex;
    align-items: flex-end;
  }
  .body_picture {
    height: 70%;
    margin-left: 20%;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  .picture {
    max-width: 100%;
    max-height: 100%;
  }

  .footer {
    height: 16%;
    padding-left: 5vw;
    padding-right: 5vw;
    display: flex;
    align-items: center;
  }
  .button_start {
    height: 12vh;
    border-radius: 1rem;
    font-size: 4vmax;
  }
}
